.App{
    overflow: hidden;
}
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: TrendSansOne;
}
h1{
    font-size: 50px;
}
/*Helpers*/
.sw{
    width: 80%;
    max-width: 1200px;
    margin: auto;
}
button {
	cursor: pointer;
    padding: 13px 20px;
    outline: none;
    border: none;
    border-radius: 18px;
}
@media screen and (max-width: 992px) {
    .btn-hc{
        display: unset;
    }
    p, span, td{
        font-size: 14px;
    }
    h1{
        font-size: 40px;
    }
    h2 {
        font-size: 30px;
    }
    h3 {
        font-size: 18px;
    }
  }
@media screen and (max-width: 768px) {
}
@media screen and (max-width: 600px) {
    p, span, td{
        font-size: 12px;
    }
    h1{
        font-size: 32px;
    }
    h2 {
        font-size: 25px;
    }
    h3 {
        font-size: 16px;
    }
    h4{
        font-size: 13px;
    }
    button {
        padding: 10px 15px
    }
  }
.Header ul {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 0;
    grid-column-gap: 15px;
    -webkit-column-gap: 15px;
            column-gap: 15px;
}
.Header {
    position: relative;
    background-color: #f0ebe7;
}
.Header a {
    font-size: 15px;
    color: #4c4557;
    text-decoration: none;
}
.Header a:hover{
    color: #000;
}
.Header a li{
    list-style: none;
}
.Header .ni{
    border-radius: 20px;
    padding: 10px;
    transition-duration: 0.2s;
}
.Header .ni:hover{
    background-color: #0000001c;
    transition-duration: 0.2s;
}
/*Responsive Header*/
.header-res {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    background-color: #dbc0ab;
    height: 100vh;
    z-index: 2;
    margin-right: -211px;
    transition-duration: 0.5s;
}
.btn-hc, .btn-ho {
    cursor: pointer;
    width: 30px;
    height: 30px;
    background-size: contain;
}
.btn-hc{
    position: absolute;
    z-index: 3;
    margin-left: -50px;
    margin-top: 32px;
}
.header-res-stat {
    display: none;
    justify-content: space-between;
    align-items: center;
}
.hide{
    display: none !important;
}
.header-res ul{
    display: block;
    padding-top: 40px;
}
.header-res ul li{
    padding: 20px 70px 20px 30px;
}
.header-res ul li:hover{
    background-color: #fff;
    color: #000;
}
.header-res-active{
    margin-right: 0;
    transition-duration: 0.5s;  
}
@media screen and (max-width: 992px) {
    .header-res-stat{
        display: flex;
    }
    .header-def{
        display: none !important;
    }
  }
  @media screen and (max-width: 768px) {
    .h-logo-wrapper {
        max-width: 220px;
    }
}
@media screen and (max-width: 600px) {
    .h-logo-wrapper {
        max-width: 150px;
    }
    .header-res ul li{
        padding: 14px 50px 14px 23px;
        font-size: 13px;
    }
    .header-res ul {
        padding-top: 20px;
    }
    .btn-hc, .btn-ho {
        width: 20px;
        height: 20px;
    }
    .btn-hc {
        margin-left: -40px;
        margin-top: 15px;
    }
  }
.cs-item{
    background-repeat: no-repeat;
    min-height: 600px;
    width: 100%;
    background-size: cover;
    background-position: center;
}
.cs-item::after {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgb(0 0 0 / 20%);
}
.c-slider {
    width: 300%;
    overflow: hidden;
    display: flex;
    transition-duration: 0.5s;
    position: relative;
}
.c-title {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    text-align: center;
}
.c-title button {
    margin: 20px 0;
    background-color: #4a4861;
    color: #fff;
    font-size: 15px;
    transition-duration: 0.3s;
}
.c-title button:hover{
    background-color: #f0ebe7;
    color: #4a4861;
    transition-duration: 0.3s;
}
.c-title h1{
    color: #fff;
}
.carousel-wrapper{
    position: relative;
    width: 100%;
    margin: auto;
    overflow: hidden;
    max-width: 1600px;
}
.cb-item {
    cursor: pointer;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    margin: auto;
    border: 1px solid #7a76a0;
    transition-duration: 0.5s;
}
.cb-item-active{
    transition-duration: 0.5s;
    background-color: #4a4861;
}
.c-btn-wrapper {
    padding: 10px 0;
    display: flex;
    grid-column-gap: 10px;
    -webkit-column-gap: 10px;
            column-gap: 10px;
    max-width: 50px;
    margin: auto;
}
@media screen and (max-width: 600px) {
    .cs-item {
        min-height: 300px;
    }
    .c-title button {
        font-size: 12px;
    }
  }

